.goBackBtn {
  padding: 0 10px;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.87;
  text-align: center;
  letter-spacing: 0.06em;
  border: 0;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  color: white;
  background-color: rgb(59, 89, 90);
}

.filmContainer {
  margin: 0 auto;
  width: auto;
  display: inline-flex;
  /* background-color: rgb(234, 0, 66); */
}

.filmDescription {
  padding: 15px 0 0 15px;
}

.filmTitle {
  font-size: 28px;
  padding-bottom: 30px;
}

.filmText {
  padding-bottom: 30px;
}

.filmCaption {
  padding-bottom: 7px;
}

.navigation {
  padding: 20px 0 30px 0;
  border-top: 1px solid rgb(234, 0, 66);
  border-bottom: 1px solid rgb(234, 0, 66);
}

.navigationTitle {
  text-align: center;
}

.navigationList {
  display: flex;
  justify-content: space-around;
  margin: 5px 0 5px 0;
}

.navigationItem {
  margin-left: 20px;
  font-size: 22px;
}

.navigationItem:hover {
  color: #fff;
}

.navigationItemActive {
  color: #fff;
  border-bottom: 2px solid rgb(234, 0, 66);
}
